import React, { lazy, Suspense } from 'react';
import { Router, Route } from 'react-router-dom';
import createBrowserHistory from 'history/createBrowserHistory';
import FormView from './components/containers/FormView';
import Dashboard from './components/containers/Dashboard';

const history = createBrowserHistory();

// const FormView = lazy(() => {
//   import('./components/containers/FormView');
// });

// const Dashboard = lazy(() => import('./components/containers/Dashboard'));

const App = () => (
  <Router history={history} basename={process.env.PUBLIC_URL}>
    <Suspense fallback={<div>...</div>}>
      <Route exact path='/' component={FormView} />
      <Route path='/success' component={Dashboard} />
    </Suspense>
  </Router>
);

export default App;
